@font-face {
    font-family: "icomoon";
    src: url("./icomoon.eot?l2elev");
    src: url("./icomoon.eot?l2elev#iefix") format("embedded-opentype"), url("./icomoon.ttf?l2elev") format("truetype"), url("./icomoon.woff?l2elev") format("woff"), url("./icomoon.svg?l2elev#icomoon") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

